/**
 *  @file _feature_tiers.sass
 *  @category 60_site_elements
 *  @theme LOCAL
 *  @description Styles the background for homepage feature tiers. */

#feature-tier-2 {
  display: grid;
  grid-template-columns: unset;
  @include bp($stack) {
    grid-template-columns: 60% 40%; }
  .region-feature-tier-2 {
    border-right: 6px solid $white;
    border-left: 6px solid $white;
    border-bottom-right-radius: $borderRadius;
    border-bottom: 6px solid $white;
    background: #B9D7CE;
    @include bp($stack) {
      border-left: unset; }
    &::after {
      content: " ";
      width: rv(6);
      height: rv(6);
      background: url("../../asset/img/svg/marks/icon_trans_white_mark.svg") no-repeat;
      display: block;
      margin-top: rv(4) * -1;
      float: right;
      @include bp($stack) {
        background: url("../../asset/img/svg/marks/green_outline_mark.svg") no-repeat;
        width: rv(7);
        height: rv(7);
        float: unset;
        margin-top: rv(6); } }

    padding: rv(2);
    a {
      color: $white;
      background: $black;
      padding: rv(-2) rv(0);
      text-decoration: unset;
      border-radius: $buttonRadius;
      font-family: $headingfont;
      font-weight: $bold;
      display: none;
      @include bp($stack) {
        display: inline-block; } } }
  #feature-1 {
    margin-top: 0;
    display: grid;
    .card-compact {
      filter: unset;
      border-bottom: 3px solid $white;
      @include bp($stack) {
        border-bottom: 6px solid $white; }
      h3 {
        position: absolute;
        margin-top: rv(4) * -1;
        border-top: unset;
        font-weight: $bold;
        padding: rv(0);
        @include bp($stack) {
          padding: 0; }
        a {
          text-decoration: unset;
          background: unset;
          padding: rv(0) 0;
          @include bp($stack) {
            padding: rv(0); }
          &:hover {
            background: unset;
            text-decoration: unset;
            color: unset; } } } }
    .representative-image {
      height: 100%;
      margin: 0;
      @include bp($stack) {
        margin: unset; }
      .item {
        height: 100%;
        figure {
          height: 100%;
          background: $clr1;
          figcaption {
            top: unset;
            bottom: rv(-1);
            left: rv(0);
            color: $white;
            font-style: normal;
            @include bp($stack) {
              bottom: rv(-7) * -1; } }
          img {
            height: 100%;
            filter: grayscale(100%);
            mix-blend-mode: multiply; } } } } } }
#feature-tier-3 {
  background: $clr2;
  text-align: center;
  .region-feature-tier-3 {
    padding: rv(1);
    @include bp($stack) {
      padding: rv(1) rv(7); }
    &::before {
      content: " ";
      width: rv(3);
      height: rv(3);
      background: url("../../asset/img/svg/marks/icon_black_mark.svg") no-repeat;
      display: block;
      margin: 0 auto;
      margin-top: rv(1); }

    a {
      color: $white;
      background: $black;
      padding: rv(-2) rv(0);
      text-decoration: unset;
      border-radius: $buttonRadius;
      font-family: $headingfont;
      font-weight: $bold; } }
  #feature-2 {
    .callout-items {
      display: grid;
      grid-template-columns: 1fr;
      grid-column-gap: 2rem;
      grid-row-gap: 2rem;
      padding: rv(0) rv(1);
      @include bp($stack) {
        grid-template-columns: 1fr 1fr 1fr; }
      .callout-item {
        background: $black;
        border-bottom-right-radius: $borderRadius; }
      figcaption {
        padding: rv(0);
        color: $white;
        text-align: left; } } } }
