/*
   @file _cards.sass
   @category 60_site_elements
   @theme LOCAL
   @component main
   @description Handles card content blocks. */

main {
  > div {
    .listing-cards {
      @include negative-content-margin(7); } } }

.cards {
  padding: rv(3);

  @include bp($stack) {
    @include separated_row(2,3);
    justify-content: flex-start; } }



.card {
  position: relative;
  .representative-image {
    &:before {
      content: "";
      height: 5px;
      width: 100%;
      background: $black;
      display: block;
      margin-bottom: 3px; } }
  .card-info-wrapper {
    margin-left: rv(1); }
  figure {
    @include proportional_thumbnail();
    border-bottom-right-radius: $borderRadius;
    a {
      img {
        filter: grayscale(100%); } }

    figcaption {
      top: unset;
      background: $black;
      border-top: 3px solid $white;

      p {
        color: $white;
        margin: rv(-2);
        font-family: $headingfont;
        font-weight: $bold;
        font-style: normal;
        font-size: font-value(-2); } } }

  h3 {
    font-family: $headingfont;
    margin-bottom: 0;
    margin-left: 0;
    @include bp($stack) {
      margin-left: rv(1); } }

  h3 + .byline {
    margin-top: rv(-3); }

  .excerpt {
    margin: rv(-2) 0;
    margin-left: 0;
    @include bp($stack) {
      margin-left: rv(1); } }
  > .btn {
    margin: rv(1) 0;
    border-radius: 10px;
    border-color: neutral(40);
    font-family: $basefont;
    margin-left: 0;
    @include bp($stack) {
      margin-left: rv(1); }
    &:hover {
      border-color: $clr4;
      color: $clr4; } } }


.card-compact {
  @include huronia_drop_shadow();

  figure {
    @include proportional_thumbnail(); }

  h3 {
    margin: 0;
    padding: 0;
    text-align: right;
    color: $white;
    @include font-size(0);
    background-color: transparent;
    border-top: rv(-6) solid $white;

    > a {
      display: block;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      padding: rv(0);
      text-align: right;
      color: inherit;
      text-decoration: underline;
      background-color: $black;

      &:hover {
        background-color: set-color(1);
        color: $black; } } } }
